<template>
  <aside v-if="this.$store.state.taskUpdated==true" hidden>
     <h1 hidden>{{ state.needToUpdate =true }}</h1>
     <!-- <h1 >IT IS True</h1> -->
     <!-- <h1 >needToUpdate: {{state.needToUpdate}}</h1> -->
     <h1 hidden>{{state.needToUpdate?goalList():'faux'}}</h1>
     <!-- <h1 >fct on : {{itSTrue()}}</h1> -->
     <h1 hidden >{{ this.$store.commit('setTaskIsNotUpdated')}}</h1>
     <h1 hidden >{{ state.needToUpdate =false }}</h1>
    
  </aside >
  <aside v-else hidden> 
    <!-- <h1 >IT IS False</h1> -->

  </aside>

  <!-- <form id="searchForm">
    <div class="input-group">
      <input type="hidden" name="page" value="1">
      <div class="input-group-prepend">
        <select class="form-select" v-model="myoption">
          <option value="on" selected>선택날</option>
          <option value="after">선택날 이전</option>
          <option value="before">선택날 이후</option>
        </select>
      </div>
      <input v-model="string2search" class="form-control" placeholder="yyyy-mm-dd">
      <div class="input-group-append" id="button-addon4">
        <button class="btn btn-outline-secondary searchBtn" type="button" @click="launchSearch()">검색</button>
        <button class="btn btn-outline-secondary searchBtn" type="button" @click="goalList()">취소</button>
      </div>
    </div>
  </form><br> -->
  <table class="table table-hover" style="table-layout: fixed">
    <thead>
      <tr>
        <th class="col-9">목표 이름</th>
        <th class="col-3">성취 날짜</th>
        <th class="col-2">성취 여부</th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td class="content " colspan="5" style="padding-left: 1em; text-align: left;">
          <RouterLink to="/goalmaker" style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">
            <img src="../assets/icones/plus-circle-fill.svg" style="width:2em;" alt="join a new circle"
              class="aligncenter text-success filter-green">
              &nbsp;&nbsp;나의 새로운 목표 만들기
          </RouterLink>
        </td>
      </tr>

      <tr v-for="(list, i) in state.lists" :key="(list, i)">
        <td class="content">
          <RouterLink :to="{name: 'goalread', params:{id:list.goalid}}"
            style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">{{ list.name }}</RouterLink>
        </td>
        <td>{{ list.dueDate }}</td>
        <!-- <td>{{ list.successQ }}{{list.unit}}</td> -->
        <td>{{ list.perCentDone }}%</td>
      </tr>
    </tbody>
  </table>
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios';
export default {
  computed: {
    updateGoalList() {
      return this.$store.commit('setTaskIsNotUpdated')
    }
  },
  setup() {
    onMounted(() => { goalList() })
    const myoption = ref('')
    const date = ref('')
    const string2search = ref('')
    const state = reactive({
      lists: '',
      needToUpdate:false
    });
    const itSTrue =  () => {
      state.needToUpdate =false

    }
    const goalList = async () => {
      state.needToUpdate =false

      const url = "http://localhost:9090/api/goalList"
      const headers = {
        "Content-Type": "application/json",
        "Authorization": sessionStorage.getItem('TOKEN'),
        "token": sessionStorage.getItem('TOKEN'),
      }
      const body = { email: sessionStorage.getItem("email") }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          state.lists = res.data
          } else {
          alert('too bad. check something')
        }
      })
    }
    const launchSearch = async () => {
      const url = "http://localhost:9090/api/searchgoal";
      const headers = {
        "Content-Type": "application/json",
        Authorization: sessionStorage.getItem('TOKEN'),
        "token": sessionStorage.getItem('TOKEN')
      };
      const body = {
        myoption: myoption.value,
        string2search: string2search.value,
        email: sessionStorage.getItem("email")
      };
      await axios.post(url, body, { headers }).then(function (res) {
        state.lists = res.data
      }).catch((error) => { console.log("the error :" + error) })
    };
    return {itSTrue, state, goalList, launchSearch, myoption, string2search }
  }
}
</script>
<style scoped>
.content {
  width: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}

.filter-green {
  filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
}
</style>