<template >
  <table class="table">
    <thead>
      <tr>
        <th scope="col"></th>
        <th scope="col">할 일</th>
        <th scope="col">상태</th>
        <th scope="col"></th>
      </tr>
    </thead>
    <th class="content " colspan="5" style="padding-left: 1em; background-color: rgba(6,90,92,0.2);">
      <RouterLink to="/calendarEntryMaker" style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">
        <img src="../assets/icones/plus-circle-fill.svg" style="width:3em;" alt="join a new circle"
          class="aligncenter text-success filter-green">&nbsp;&nbsp;새로운 할 일 만들기
      </RouterLink>
    </th>
    <tbody v-for="(list, i) in state.lists" :key="(list, i)">
      <tr v-if="list.dates != state.tmpDayNb" class="table-success">
        <th scope="row">{{list.dates.substring(5)}} </th>
        <td>{{list.goalName}}</td>
        <td>{{list.qdone}} / {{list.qgoal}} {{list.unit}} </td>
        <span hidden> {{ state.tmpDayNb = list.dates }}</span>
        <td>
          <button class="btn btn-outline-success" data-bs-toggle="tooltip" data-bs-placement="top"
            title="to complete the task" @click="beDone(i),this.$store.commit('setTaskIsUpdated')">✓</button>
          <button class="btn btn-outline-light" data-bs-toggle="tooltip" data-bs-placement="top"
            title="delete the task for ever" @click="deleteEntry(list),this.$store.commit('setTaskIsUpdated')">X</button>
        </td>
      </tr>
      <tr v-else>
        <span hidden> {{ state.tmpDayNb = list.dates }}</span>
        <th scope="row"></th>
        <td>{{list.goalName}}</td>
        <td>{{list.qdone}} / {{list.qgoal}} {{list.unit}} </td>
        <td>
          <button class="btn btn-outline-success" data-bs-toggle="tooltip" data-bs-placement="top"
            title="to complete the task" @click="beDone(i)">✓</button>
          <button class="btn btn-outline-light" data-bs-toggle="tooltip" data-bs-placement="top"
            title="delete the task for ever" @click="deleteEntry(list)">X</button>
        </td>
      </tr>
    </tbody>
  </table>
  <Teleport to="body">
    <modal2 :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h2>
          <div class="snowflakes" aria-hidden="true">
            <div class="snowflake">            ❅            </div>
            <div class="snowflake">            ❅            </div>
            <div class="snowflake">            ❆            </div>
            <div class="snowflake">            ❄            </div>
            <div class="snowflake">            ❅            </div>
            <div class="snowflake">            ❆            </div>
            <div class="snowflake">            ❄            </div>
            <div class="snowflake">            ❅            </div>
            <div class="snowflake">            ❆            </div>
            <div class="snowflake">            ❄            </div>
          </div>
        </h2>
      </template>
      <template #body>
        <img style="width:55em" alt="user icon" src="../assets/images/pictures/congraturations3.jpg">
        <div>
          <h5>축하합니다!! 할 일을 완료 하였습니다!!</h5>
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="btnCon">Thank you</el-button>
      </template>
    </modal2>
  </Teleport>
</template>            
<script>
  import { reactive,computed  } from 'vue';
  import axios from 'axios';
  import { onMounted } from 'vue'
  import Modal2 from './teleport/Modal2.vue'
  import { useRouter } from 'vue-router';
  import { Tooltip } from 'bootstrap/dist/js/bootstrap.esm.min.js'
  export default {
    components: {
      Modal2,
    },
    computed:{
      colorIfDone(listIndex){
        console.log('listIndex computed :'+listIndex)
      }
    },
    mounted() {
      Array.from(document.querySelectorAll('button[data-bs-toggle="tooltip"]'))
        .forEach(tooltipNode => new Tooltip(tooltipNode))
    },
    setup() {
      const router = useRouter();
      onMounted(() => {
        getData()
      })
      const state = reactive({
        lists: "",
        showModal: false,
        email: sessionStorage.getItem("email"),
        token: sessionStorage.getItem("TOKEN"),
      });
      const getData = async () => {
        const url = "http://localhost:9090/api/calendarlistbyuid";
        const headers = {
          "Content-Type": "application/json",
          "Authorization": state.token,
          "token": state.token,
          "test": "test",
        };
        const body = { email: state.email };
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.data != null) {
            state.lists = res.data;
          }
          else {
            alert("bad.");
          }
        });
      };
      const beDone = async (i) => {
        const url = "http://localhost:9090/api/beDone";
        const headers = {
          "Content-Type": "application/json",
          "Authorization": state.token,
          "token": state.token,
          "test": "test",
        };
        const body = { calendarid: state.lists[i].calendarid };
        await axios.post(url, body, { headers }).then(function (res) {
            state.showModal = true;
            getData()
        }).catch((err)=>{console.log("err :"+err)});
      }
      const deleteEntry = async (list) => {
        const url = "http://localhost:9090/api/deleteCalendarEntry"
        const headers = {
          'Content-Type': 'application/json',
          Authorization: sessionStorage.getItem('TOKEN'),
          token: sessionStorage.getItem('TOKEN'),
          'name': 'test list'
        }
        const body = { calendarid: list.calendarid }
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.status == 200) {
            setTimeout(getData(), 1000)
          } else {
            alert('bad.')
          }
        }).catch(function (error) {
          if (error) {
            alert('something went wrong..')
            console.log('Error', error.message);
          }
        })
      }
      const btnCon = () => {
        state.showModal = false;
      };
      return { state,  getData, beDone, btnCon, deleteEntry,};
    },
  }
  </script >
<style >
.filter-green {
  filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}
.snowflake {
  color: #fff;
  font-size: 1em;
  font-family: Arial;
  text-shadow: 0 0 1px #000;
}
@-webkit-keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}@-webkit-keyframes snowflakes-shake{0%{-webkit-transform:translateX(0px);transform:translateX(0px)}50%{-webkit-transform:translateX(80px);transform:translateX(80px)}100%{-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}@keyframes snowflakes-shake{0%{transform:translateX(0px)}50%{transform:translateX(80px)}100%{transform:translateX(0px)}}.snowflake{position:fixed;top:-10%;z-index:9999;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;-webkit-animation-name:snowflakes-fall,snowflakes-shake;-webkit-animation-duration:10s,3s;-webkit-animation-timing-function:linear,ease-in-out;-webkit-animation-iteration-count:infinite,infinite;-webkit-animation-play-state:running,running;animation-name:snowflakes-fall,snowflakes-shake;animation-duration:10s,3s;animation-timing-function:linear,ease-in-out;animation-iteration-count:infinite,infinite;animation-play-state:running,running}.snowflake:nth-of-type(0){left:1%;-webkit-animation-delay:0s,0s;animation-delay:0s,0s}.snowflake:nth-of-type(1){left:10%;-webkit-animation-delay:1s,1s;animation-delay:1s,1s}.snowflake:nth-of-type(2){left:20%;-webkit-animation-delay:6s,.5s;animation-delay:6s,.5s}.snowflake:nth-of-type(3){left:30%;-webkit-animation-delay:4s,2s;animation-delay:4s,2s}.snowflake:nth-of-type(4){left:40%;-webkit-animation-delay:2s,2s;animation-delay:2s,2s}.snowflake:nth-of-type(5){left:50%;-webkit-animation-delay:8s,3s;animation-delay:8s,3s}.snowflake:nth-of-type(6){left:60%;-webkit-animation-delay:6s,2s;animation-delay:6s,2s}.snowflake:nth-of-type(7){left:70%;-webkit-animation-delay:2.5s,1s;animation-delay:2.5s,1s}.snowflake:nth-of-type(8){left:80%;-webkit-animation-delay:1s,0s;animation-delay:1s,0s}.snowflake:nth-of-type(9){left:90%;-webkit-animation-delay:3s,1.5s;animation-delay:3s,1.5s}
.demo {
  font-family: 'Raleway', sans-serif;
  color:#fff;
    display: block;
    margin: 0 auto;
    padding: 15px 0;
    text-align: center;
}
.demo a{
  font-family: 'Raleway', sans-serif;
color: #000;    
}
</style>